/* Sign up styling starts here */

.modal-content{
    border-radius:  15px;
    border: 0px;
    outline: none;
  }
  
  .modal-backdrop {
    background-color: #003c80 !important;
  }
  
  .modal-header{
    text-align: right !import;
  }
  .close{
    position: absolute;
    right: 12px;
    top: 12px;
  }
  .close:hover{
    cursor: pointer !important;
  }
  
  .already-sign-in{
      font-family: 'Gilmer Bold';
      font-size: 14px;
      line-height: 17px;
      font-style: normal;
      font-weight: 900;
      letter-spacing: 0em;
      color: #374B58;
  }
  
  .logo-wrapper{
    display: flex;
  }
  
  
  .already-sign-in p{
  
  }
  
  .signin{
     text-decoration: none !important;
  }
  
  .sign-up{
    
    color: #374B58;
    font-size: 18px;
    font-style: normal;
    font-weight: 900;
    line-height: 29px;
    letter-spacing: 0px;
    font-family: 'Gilmer Heavy'; 
  
  }
  
  .form-group {
     margin: 24px 0px;
  }
  
  
  .form-field{
    border-radius:  10px;
    height: 60px;
    font-family: Gilmer Regular;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 17px;
    letter-spacing: 0px;
    text-align: left;
    color: #8897A0;
   ;
  }
  
  .form-control{
    height: 60px !important;
    padding: 0px;
  }
  
  .password-notes{
    font-family: Gilmer Medium;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 14px;
    letter-spacing: 0em;
    color: #8897A0;
    margin-left: 1px;
  }
  
  .form-check-label{
    font-family: Gilmer Medium;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: 12px;
    letter-spacing: 0px;
    text-align: left;
    color: #8897A0;
  
  }
  
  .submit{
    border-radius:  10px;
    height: 60px;
    background-color:#0075FA;
    border: 1px solid #A5B0B74D;
    font-family: Gilmer Bold;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: center;
    color: white;
  
  }
  
  input.form-control, input.form-control:focus, select.form-select{
      box-shadow: none;
     -webkit-box-shadow: none;
     -moz-box-shadow: none;
     -moz-transition: none;
     -webkit-transition: none;
     color: #8897A0;
  
  }
  
  button.form-control{
      box-shadow: none !impotant;
     -webkit-box-shadow: none !important;
     -moz-box-shadow: none !impotant;
     -moz-transition: none !impotant;
     -webkit-transition: none !impotant;
  }
  
  button.btn {
      box-shadow: none !impotant;
     -webkit-box-shadow: none !important;
     -moz-box-shadow: none !impotant;
     -moz-transition: none !impotant;
     -webkit-transition: none !impotant;
  }
  
  select:focus {
    outline: 0;
    box-shadow: none !important;
    color: #8897A0;
  }
  
  
  /*
  ::placeholder { 
   color: #8897A0 !important;
    opacity: 1; 
  }
  
  :-ms-input-placeholder {
    color: #8897A0 !important;;
  }
  
  ::-ms-input-placeholder {
    color: #8897A0 !important;
  }
  */
  
  .form-floating label{
    font-family: Gilmer Regular;
    color: #8897A0 !important;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 17px;
    letter-spacing: 0px;
    padding-top: 17px !important;
  }
  
  
.form-group, .phone-column{
    position: relative;
 }
 .phone-column .displayOnFocus{
   top: 5px;
   left:15px;
 } 
 
 .error{
   border: 1px solid #EA4336;
   border-radius: 10px;
 }
 
 .errorMessage{
   color:#EA4336;
   font-family: Gilmer Regular;
   font-style: normal;
   font-weight: normal;
   font-size: 10px;
   line-height: 12px;
 }
  
  @media(max-width: 768px){
    .textlogo{
      margin: 0px !important;
    }
    .already-sign-in-wrapper{
      margin: 0px !important;
    }
  
   
  }
  