/*Navbar Styling Starts from Here*/
nav{
    background: #006BE6;
    padding-top:15px;
    padding-bottom: 8px;
  }
  
  .nav-link {
    padding: .4rem .8rem;
   
  }
  
  .nav-left{
    font-family: Pusia-DemiBold;
    font-size: 18px;
    line-height: 21px;
  }
  
  .nav-item{
   margin-bottom: -8px;  
  }
  
  .nav-right{
    font-family: Gilmer Bold;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
  }
  
  .nav-item a{
    text-decoration: none;
  }
  
  .nav-link {
    color:white;
  }
    
  .nav-link:hover{
    color: #FDC535;
    cursor: pointer;
  }
  
  nav .nav-item span:hover{
    color: #FDC535;
    cursor: pointer;
  }
  
  .nav__userIcon:hover{
    color: #FDC535 !important;
    cursor: pointer;
  }
  
  
    nav .navbar-nav li a:hover{
      color: #FDC535 !important;
    }
  
  
  .active{
    color: #FDC535 !important;
    background: #1A85FF;
    border-radius: 6px 6px 0px 0px;
    padding-bottom: 20px;
  }
  
  .header-image{
    background: #0075FA;
  
  }

  @media (max-width: 768px){
    nav{
        padding-top:15px;
        padding-bottom: 8px;
      }
      
      .nav-link {
        padding: .4rem .8rem;
       
      }
      
      .nav-left{
        font-family: Pusia-DemiBold;
        font-size: 16px;
        line-height: 21px;
      }
      
      .nav-item{
       margin-bottom: 0px;  
      }
      
      .nav-right{
        font-family: Gilmer Bold;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 22px;
      }
      
      .nav-item a{
        text-decoration: none;
      }
      
      .nav-link {
        color:white;
      }
      
      .nav-link:hover{
        color: #FDC535;
        cursor: pointer;
      }
      
      nav .nav-item span:hover{
        color: #FDC535;
        cursor: pointer;
      }
      
      .nav__userIcon:hover{
        color: #FDC535 !important;
        cursor: pointer;
      }
      
      
        nav .navbar-nav li a:hover{
          color: #FDC535 !important;
        }
      
      
      .active{
        color: #FDC535 !important;
        background: #1A85FF;
        border-radius: 6px 6px 0px 0px;
        padding-bottom: 10px;
      }
      
      .header-image{
        background: #0075FA;
        margin-bottom: -10px;    
      }

      .header-image img{
        width: 120px !important;
        object-fit: contain;
      }
      
      .arrow__right:hover{
        cursor: pointer;
      }
  }
  