/*Hero Section Styling Start from here*/

.hero-section{
    min-height: 602px;
    background-image:url('../img/Background-pattern.svg') ;
    background-size: cover;
  }
  
  
  .fast-shoping{
    margin-left: 100px;
    font-family: Gilmer Bold;
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: 58px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFFFFF;
    max-width: 623px ;
    
  }
  
  .hero-left{
   
  }
  
  .ilustration1{
    margin-right: 140px;
  }
  
  .hero-left{
    height: 602px;
    position: relative;
    
  }
  
  .hero-right{
    height: 602px;
    position: relative;
  }
  
  .goods{
   position: absolute;
   right: 0px;
   bottom:0px;
  }
  
  .image-wrapper{
    position: absolute;
    left:0px;
    top: 48%  ;
  }
  
 
  .form-panel{
    max-width: 420px;
    min-height: 344px;
    background: #FFFFFF;
    border: 1px solid rgba(165, 176, 183, 0.3);
    border-radius: 10px;
    margin-top: 90px;
    }
  
  
    .form-panel h4{
    font-family: Gilmer  Bold;
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: 0px;
    color: #374B58;
      
  }
  
  .continue-with-phone{
    border-radius:  10px;
    height: 60px;
    background-color:#0075FA;
    border: 1px solid #A5B0B74D;
    font-family: Gilmer Bold;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: center;
    color: white;
  }
  
  .container-fluid{
    padding: 0px 120px !important;
  }

  @media(max-width:768px){

    .hero-section{
        min-height: 30px;
        background-image: none;
        background-color: white;
        background-size: none;
      }


      .form-panel{
        max-width: 420px;
        min-height: 344px;
        background: #FFFFFF;
        border: 0px solid rgba(165, 176, 183, 0.3);
        border-radius: 10px;
        margin-top: 90px;
        } 
  
    .fast-shoping{
      margin-left: 5px;
      font-family: Gilmer Regular;
      font-size: 38px;
      font-style: normal;
      font-weight: 700;
      line-height: 58px;
      letter-spacing: 0em;
      text-align: left;
      color: #FFFFFF;
      max-width: 623px ;
    }
  
    .herologo{
      margin-top: 86px;
      margin-left: 5px;
  
    }
  
    
     .form-panel{
      max-width: 95%;
      margin-left: auto;
      margin-right: auto;   
      }
  
     
  
      .image-wrapper{
        position: absolute;
        width: 100%;
        left:0px;
        top: 60%;
      }
  
      .ilustration1 .ilustration2{
       display: none;
      }
  
  
      .goods{
        display: none;
       }
       
       .container-fluid{
        padding: 0px 10px !important;
      }
      
      .login__or__register{
        font-family: Gilmer Bold;
        font-style: normal;
        font-weight: bold;
        font-size: 16px !important;
        line-height: 19px;        
        color: #0075FA !important;
        
      }

      form{
          margin-top: -50px;
      }
  
  }
  
