*{
  margin: 0px;
}

html, body{
  height: 100%;
}

body{
   font-size: 14px;
   background-color:#F9FAFC;
   overflow-x: hidden;
}



/* Home Page Styling Starts From Here */




/* Category Section*/
.browse__gallery_heading{
  font-size: 24px !important;

}

.category-section{
   padding :31px 0px;
   box-shadow: 0px 0px 5px rgba(19, 129, 255, 0.2);
}

.section-heading{
  font-family: Gilmer Bold;
  font-size: 24px !important;
  font-style: normal;
  font-weight: 700;
  line-height: 29px;
  letter-spacing: 0px;
  text-align: left;
  color: #374B58;

}


.categories{
  margin-top: 33px;
  display: flex;
  flex-wrap: wrap;
} 

.category{
  padding: 10px 10px;
  margin: 10px;
}


.category a{
  text-decoration: none;
}

.category-image{
  display: block;
  margin: auto;
} 

.cat-wrapper{
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-radius: 10px;
}

.category-caption{
  font-family: Gilmer Medium;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #374B58;
  margin-top: 10px;

}
/* there-is-more section */


/* shopping section*/
.shopping-section{
  padding-top: 20px;
  padding-bottom: 0px;
  background-color: #FAEFD4;
  overflow:hidden;
 
}

.shopping-right{
  padding-top:20px;
  height: 401.58px !important;
}

.shopping-left{

  height: 401.58px !important;
}

.shopping-section img{
  opacity: 1 !important;
}

.shopping-section .icon{
  max-width: 504px;
  margin-left: auto;
  margin-right: 42px;
}
.shopping-section h3{
  margin-left: auto;
  margin-right: auto;
  font-family: Gilmer Bold;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  color: #374B58;
}

.download-text{
  font-family: Gilmer Bold;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  color: #374B58;
}


/*Favourite section*/

.favourite-section{
  
}


.jeetar{
  background: linear-gradient(135.96deg, #66ADFF 0%, #0077FF 101.74%);
  border-radius: 10px;
}

.favourite-top h2{
  font-family: Gilmer Bold;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 43px;
  color: #0075FA;
}

.favourite-top h2{
  margin-right: 24px;
}



.favourite-top img{
  margin-right: 24px;
}

.favourite-head{

  font-family: Gilmer Bold;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #374B58;
}

.favourite-container{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.favourite{
  width: 220px;
  padding: 2em;
  position:relative;
  background-color: white;
  border: solid .5px #eff4fad3;;

}

.add-to-cart{
  top:10px;
  right:20px;
  position: absolute;
  cursor: pointer;
}


.add-to-cart img{
  width: 35px;
}

.fav-wrapper{
  text-align: center;
}

.favourite p{
  margin-bottom: 4px;
  margin-bottom: 4px;
 
}
.price{
font-family: Gilmer Bold;
font-style: normal;
font-weight: bold;
font-size: 16px;
line-height: 19px;
color: #0075FA;
margin-top: 10px;
}

.name{
font-family: Gilmer Regular;
font-style: normal;
font-weight: 600;
font-size: 14px;
line-height: 17px;
color: #374B58;
}

.weigth{

font-family: Gilmer Light;
font-style: normal;
font-weight: normal;
font-size: 14px;
line-height: 17px;
color: #5E8097;
}


.place-order img, .get-order img, .unbeatable img{
  margin-bottom: 20px;
  margin-top: 20px;
}

.place-order p, .get-order p, .unbeatable p{
  max-width: 261px;
  margin: auto;
  height: 38px;
  font-family: Gilmer Regular;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #374B58;
  
}

.place-order h6, .get-order h6, .unbeatable h6{
  font-family: Gilmer Bold;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #374B58;
  
}


/* Footer section*/
.footer-section{
  min-height: 452px;
  background: #0D0D0D;
}

.footer-link-wrapper{
  list-style: none;

}

.footer-link-wrapper li{
    margin: 20px 0px;
}


.footer-link-wrapper li a{
  text-decoration: none;
  color:white;
  font-family: Gilmer Light;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
}

.store a{
  background-color: white;
  width: 125px;
  height: 53px;
  display: inline-block;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.08);
  border-radius: 10px;
}

.store img{
  margin: 15px;
}

.social-facebook{
  width: 1000px;
}

.social img{
   width: 44px;
   height:44px;
}
